/* =========================
   Action Menu Component
========================= */
function ActionMenu({
  openUp,
  onEdit,
}: {
  openUp?: boolean;
  onEdit: () => void;
}) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    function handleClickOutside(e: MouseEvent) {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    }
    if (open) document.addEventListener("mousedown", handleClickOutside);
    return () =>
      document.removeEventListener("mousedown", handleClickOutside);
  }, [open]);

  return (
    <div ref={ref} className="relative">
      <button
        onClick={() => setOpen((p) => !p)}
        className="rounded-full p-2 hover:bg-gray-100 dark:hover:bg-gray-700"
      >
        ‚ãÆ
      </button>

      {open && (
        <div
          className={`absolute right-0 z-50 w-36 rounded-lg border bg-white shadow-lg
          dark:border-gray-700 dark:bg-gray-900
          ${openUp ? "bottom-full mb-2" : "top-full mt-2"}`}
        >
          <button
            onClick={() => {
              onEdit();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm hover:bg-gray-50 dark:hover:bg-gray-800"
          >
            ‚úèÔ∏è Edit
          </button>

          <button
            onClick={() => setOpen(false)}
            className="flex w-full gap-2 px-4 py-2 text-sm text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20"
          >
            üóë Delete
          </button>
        </div>
      )}
    </div>
  );
}

/* =========================
   User Table Component
========================= */
export default function UserTable() {
  const [search, setSearch] = useState("");
  const [openModal, setOpenModal] = useState(false);
  const [mode, setMode] = useState<"add" | "edit">("add");
  const [selectedUser, setSelectedUser] =
    useState<UserFormData | undefined>();

  const filteredUsers = usersData.filter(
    (user) =>
      user.name.toLowerCase().includes(search.toLowerCase()) ||
      user.email.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="w-full rounded-xl bg-white px-6 pb-6 pt-0 dark:bg-gray-900">
      {/* Title */}
      <h2 className="mb-4 text-xl font-semibold text-gray-800 dark:text-white">
        Users
      </h2>

      {/* Top Bar */}
      <div className="mb-4 flex items-center justify-between">
        <Input
          placeholder="Search users..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-64"
        />
        <Button
          size="sm"
          onClick={() => {
            setMode("add");
            setSelectedUser(undefined);
            setOpenModal(true);
          }}
        >
          + Add User
        </Button>
      </div>

      {/* Table */}
      <div className="overflow-x-auto">
        <table className="w-full border-collapse">
          <thead className="bg-gray-50 dark:bg-gray-800">
            <tr className="text-sm text-gray-600 dark:text-gray-300">
              <th className="py-3 text-left font-semibold">User</th>
              <th>Email</th>
              <th>Phone</th>
              <th>Address</th>
              <th>Role</th>
              <th>Created By</th>
              <th className="text-right">Action</th>
            </tr>
          </thead>

          <tbody>
            {filteredUsers.map((user, index) => (
              <tr
                key={user.id}
                className="border-b last:border-0 hover:bg-gray-50 dark:hover:bg-gray-800 transition"
              >
                {/* User */}
                <td className="py-4">
                  <div className="flex items-center gap-3">
                    <div className="flex h-9 w-9 items-center justify-center rounded-full bg-blue-100 text-sm font-semibold text-blue-700">
                      {user.name.charAt(0)}
                    </div>
                    <div>
                      <p className="font-medium text-gray-800 dark:text-white">
                        {user.name}
                      </p>
                      <p className="text-xs text-gray-500">{user.email}</p>
                    </div>
                  </div>
                </td>

                {/* Email */}
                <td className="py-4 text-sm text-gray-600 dark:text-gray-300">
                  {user.email}
                </td>

                {/* Phone */}
                <td className="py-4 font-mono text-sm text-gray-700 dark:text-gray-300">
                  {user.phone}
                </td>

                {/* Address */}
                <td className="py-4 text-sm text-gray-600">
                  {user.address}
                </td>

                {/* Role */}
                <td className="py-4">
                  <span
                    className={`rounded-full px-3 py-1 text-xs font-semibold
                      ${
                        user.role === "Admin"
                          ? "bg-red-100 text-red-700"
                          : user.role === "Editor"
                          ? "bg-yellow-100 text-yellow-700"
                          : "bg-green-100 text-green-700"
                      }`}
                  >
                    {user.role}
                  </span>
                </td>

                {/* Created By */}
                <td className="py-4 text-sm text-gray-600">
                  {user.createdBy}
                </td>

                {/* Action */}
                <td className="py-4 text-right">
                  <ActionMenu
                    openUp={index === filteredUsers.length - 1}
                    onEdit={() => {
                      setMode("edit");
                      setSelectedUser({
                        name: user.name,
                        email: user.email,
                        phone: user.phone,
                        address: user.address,
                        role: user.role,
                        gender: user.gender,
                        password: "",
                        confirmPassword: ""
                      });
                      setOpenModal(true);
                    }}
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Modal */}
      <UserFormModal
        open={openModal}
        mode={mode}
        initialData={selectedUser}
        onClose={() => setOpenModal(false)}
        onSubmit={(data) => {
          console.log(mode === "add" ? "ADD USER" : "UPDATE USER", data);
          setOpenModal(false);
        }}
      />
    </div>
  );
}
