"use client";
import { useEffect, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";

export interface UserFormData {
  name: string;
  email: string;
  phone: string;
  address: string;
  gender: string;
  role: string;
  password: string;
  confirmPassword: string;
}

interface Props {
  open: boolean;
  mode: "add" | "edit";
  initialData?: UserFormData;
  onClose: () => void;
  onSubmit: (data: UserFormData) => void;
}

const emptyForm: UserFormData = {
  name: "",
  email: "",
  phone: "",
  address: "",
  gender: "",
  role: "",
  password: "",
  confirmPassword: "",
};

export default function UserFormModal({
  open,
  mode,
  initialData,
  onClose,
  onSubmit,
}: Props) {
  const [form, setForm] = useState<UserFormData>(emptyForm);
  const [errors, setErrors] = useState<Partial<Record<keyof UserFormData, string>>>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    if (mode === "edit" && initialData) {
      setForm({
        ...initialData,
        password: "",
        confirmPassword: "",
      });
    } else {
      setForm(emptyForm);
    }
    setErrors({});
  }, [mode, initialData, open]);

  useEffect(() => {
    const handleEsc = (e: KeyboardEvent) => e.key === "Escape" && onClose();
    document.addEventListener("keydown", handleEsc);
    return () => document.removeEventListener("keydown", handleEsc);
  }, [onClose]);

  const validateForm = (): boolean => {
    const newErrors: Partial<Record<keyof UserFormData, string>> = {};
    
    // Only validate essential fields
    if (!form.name.trim()) newErrors.name = "Name is required";
    if (!form.email.trim()) newErrors.email = "Email is required";
    if (!form.role) newErrors.role = "Role is required";

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async () => {
    if (!validateForm() || isSubmitting) return;
    
    setIsSubmitting(true);
    try {
      const { confirmPassword, ...submitData } = form;
      await onSubmit(submitData as UserFormData);
    } finally {
      setIsSubmitting(false);
    }
  };

  const update = (key: keyof UserFormData, value: string) => {
    setForm(prev => ({ ...prev, [key]: value }));
    if (errors[key]) {
      setErrors(prev => ({ ...prev, [key]: "" }));
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit();
    }
  };

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 backdrop-blur-sm">
      <div 
        className="w-full max-w-4xl mx-4 rounded-2xl bg-white shadow-2xl dark:bg-gray-900 flex flex-col max-h-[85vh]"
        onClick={(e) => e.stopPropagation()}
      >
        {/* Header */}
        <div className="p-6 border-b dark:border-gray-800 bg-gradient-to-r from-gray-50 to-white dark:from-gray-900 dark:to-gray-800">
          <div className="flex justify-between items-center">
            <div>
              <h3 className="text-xl font-bold text-gray-900 dark:text-white">
                {mode === "add" ? "Add New User" : "Edit User Profile"}
              </h3>
              <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                {mode === "add" ? "Fill in the details to create a new user account" : "Update user information"}
              </p>
            </div>
            <button
              onClick={onClose}
              className="w-10 h-10 rounded-full hover:bg-gray-100 dark:hover:bg-gray-800 flex items-center justify-center transition-colors text-2xl text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-300"
              aria-label="Close"
            >
              √ó
            </button>
          </div>
        </div>

        {/* Scrollable Form Content */}
        <div className="flex-1 overflow-y-auto p-6 md:p-8" onKeyDown={handleKeyDown}>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {/* Left Column */}
            <div className="space-y-6">
              <FormField 
                label="Full Name" 
                required 
                error={errors.name}
               
              >
                <Input
                  value={form.name}
                  onChange={(e) => update("name", e.target.value)}
                  placeholder="John Doe"
                  error={!!errors.name}
                  className="w-full"
                />
              </FormField>

              <FormField 
                label="Phone Number" 
                error={errors.phone}
              >
                <Input
                  value={form.phone}
                  onChange={(e) => update("phone", e.target.value)}
                  placeholder="+1 (555) 123-4567"
                  type="tel"
                />
              </FormField>

              <FormField 
                label="Role" 
                required 
                error={errors.role}
              >
                <div className="relative">
                  <select
                    className="w-full rounded-lg border border-gray-300 dark:border-gray-700 bg-white dark:bg-gray-800 px-4 py-3 pr-10 text-gray-900 dark:text-white focus:border-blue-500 focus:ring-2 focus:ring-blue-500/20 focus:outline-none transition-all appearance-none"
                    value={form.role}
                    onChange={(e) => update("role", e.target.value)}
                  >
                    <option value="" className="text-gray-400">Select a role</option>
                    <option value="User" className="text-gray-900 dark:text-white">User</option>
                    <option value="Admin" className="text-gray-900 dark:text-white">Administrator</option>
                    <option value="Manager" className="text-gray-900 dark:text-white">Manager</option>
                    <option value="Editor" className="text-gray-900 dark:text-white">Editor</option>
                  </select>
                  <div className="absolute right-3 top-1/2 transform -translate-y-1/2 pointer-events-none">
                    <svg className="w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                    </svg>
                  </div>
                </div>
              </FormField>

              <FormField 
                label={mode === "add" ? "Confirm Password" : "Confirm New Password"} 
                required={false}
                error={errors.confirmPassword}
              >
                <Input
                  type="password"
                  value={form.confirmPassword}
                  onChange={(e) => update("confirmPassword", e.target.value)}
                  placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                  className="w-full"
                />
              </FormField>
            </div>

            {/* Right Column */}
            <div className="space-y-6">
              <FormField 
                label="Email Address" 
                required 
                error={errors.email}
              >
                <Input
                  type="email"
                  value={form.email}
                  onChange={(e) => update("email", e.target.value)}
                  placeholder="john.doe@example.com"
                  error={!!errors.email}
                  className="w-full"
                />
              </FormField>

              <FormField 
                label="Gender"
              >
                <div className="flex gap-6">
                  <label className="flex items-center gap-3 cursor-pointer">
                    <div className="relative">
                      <input
                        type="radio"
                        name="gender"
                        value="Male"
                        checked={form.gender === "Male"}
                        onChange={(e) => update("gender", e.target.value)}
                        className="sr-only peer"
                      />
                      <div className="w-5 h-5 rounded-full border-2 border-gray-300 dark:border-gray-600 peer-checked:border-blue-500 flex items-center justify-center transition-colors">
                        {form.gender === "Male" && (
                          <div className="w-2.5 h-2.5 rounded-full bg-blue-500"></div>
                        )}
                      </div>
                    </div>
                    <span className="text-gray-700 dark:text-gray-300">Male</span>
                  </label>

                  <label className="flex items-center gap-3 cursor-pointer">
                    <div className="relative">
                      <input
                        type="radio"
                        name="gender"
                        value="Female"
                        checked={form.gender === "Female"}
                        onChange={(e) => update("gender", e.target.value)}
                        className="sr-only peer"
                      />
                      <div className="w-5 h-5 rounded-full border-2 border-gray-300 dark:border-gray-600 peer-checked:border-blue-500 flex items-center justify-center transition-colors">
                        {form.gender === "Female" && (
                          <div className="w-2.5 h-2.5 rounded-full bg-blue-500"></div>
                        )}
                      </div>
                    </div>
                    <span className="text-gray-700 dark:text-gray-300">Female</span>
                  </label>
                </div>
              </FormField>

              <FormField 
                label={mode === "add" ? "Password" : "New Password"} 
                required={false}

              >
                <div className="relative">
                  <Input
                    type="password"
                    value={form.password}
                    onChange={(e) => update("password", e.target.value)}
                    className="w-full"
                  />
                </div>
              </FormField>

              <FormField 
                label="Address" 
              >
                <textarea
                  value={form.address}
                  onChange={(e) => update("address", e.target.value)}
                  placeholder="123 Main Street, City, State, ZIP Code"
                  className="w-full rounded-lg border border-gray-300 dark:border-gray-700 bg-white dark:bg-gray-800 px-4 py-3 text-gray-900 dark:text-white focus:border-blue-500 focus:ring-2 focus:ring-blue-500/20 focus:outline-none transition-all min-h-[100px] resize-none"
                  rows={3}
                />
              </FormField>
            </div>
          </div>

          {/* Error Summary */}
          {Object.keys(errors).some(key => errors[key as keyof UserFormData]) && (
            <div className="mt-8 p-4 bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-xl">
              <div className="flex items-start gap-3">
                <div className="w-6 h-6 rounded-full bg-red-100 dark:bg-red-900 flex items-center justify-center flex-shrink-0 mt-0.5">
                  <span className="text-red-600 dark:text-red-400 text-sm font-bold">!</span>
                </div>
                <div>
                  <p className="font-medium text-red-800 dark:text-red-300">Please fix the following errors:</p>
                  <ul className="mt-2 space-y-1">
                    {Object.entries(errors)
                      .filter(([_, value]) => value)
                      .map(([key, value]) => (
                        <li key={key} className="text-sm text-red-700 dark:text-red-400 flex items-center gap-2">
                          <span className="w-1.5 h-1.5 rounded-full bg-red-500" />
                          {value}
                        </li>
                      ))}
                  </ul>
                </div>
              </div>
            </div>
          )}
        </div>

        {/* Fixed Footer with Buttons */}
        <div className="p-6 border-t dark:border-gray-800 bg-gray-50/50 dark:bg-gray-900/50 backdrop-blur-sm">
          <div className="flex flex-col sm:flex-row justify-between items-center gap-4">
            <div className="text-sm text-gray-500 dark:text-gray-400">
              {mode === "add" ? (
                <>All fields marked with <span className="text-red-500">*</span> are required</>
              ) : (
                <>Update user information carefully</>
              )}
            </div>
            <div className="flex gap-3 w-full sm:w-auto">
              <Button
                variant="outline"
                onClick={onClose}
                className="flex-1 sm:flex-none px-8"
              >
                Cancel
              </Button>
              <Button
                onClick={handleSubmit}
                disabled={isSubmitting}
                className="flex-1 sm:flex-none px-8"
              >
                {isSubmitting ? (
                  <span className="flex items-center gap-2">
                    <svg className="animate-spin h-4 w-4" viewBox="0 0 24 24">
                      <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" fill="none" />
                      <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                    </svg>
                    Processing...
                  </span>
                ) : mode === "add" ? (
                  "Create User"
                ) : (
                  "Update User"
                )}
              </Button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

interface FormFieldProps {
  label: string;
  children: React.ReactNode;
  error?: string;
  helpText?: string;
  required?: boolean;
  fullWidth?: boolean;
}

function FormField({ label, children, error, helpText, required, fullWidth }: FormFieldProps) {
  return (
    <div className={fullWidth ? "md:col-span-2" : ""}>
      <div className="flex justify-between items-center mb-2">
        <Label className="text-gray-900 dark:text-white font-medium">
          {label}
          {required && <span className="text-red-500 ml-1">*</span>}
        </Label>
        {helpText && (
          <span className="text-xs text-gray-500 dark:text-gray-400">{helpText}</span>
        )}
      </div>
      {children}
      {error && (
        <p className="mt-2 text-sm text-red-600 dark:text-red-400 flex items-center gap-1">
          <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7 4a1 1 0 11-2 0 1 1 0 012 0zm-1-9a1 1 0 00-1 1v4a1 1 0 102 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
          </svg>
          {error}
        </p>
      )}
    </div>
  );
}